body {
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 13px;
  line-height: 19px;
  color: #595959;
  background-color: transparent;
}

div,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
button,
input,
select,
pre,
code,
ul,
ol,
li {
  transition: background-color .1s ease-in-out, color .1s ease-in-out, border .1s ease-in-out, background-image .1s ease-in-out;
}

.wrapper,
.wrapper-boxed {
  position: relative;
  min-height: 100vh;
}

.wrapper-boxed {
  width: 88%;
  margin-right: auto;
  margin-left: auto;
  color: #595959;
  background-color: white;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
  .wrapper-boxed .container {
    width: 680px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
  .wrapper-boxed .container {
    width: 900px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
  .wrapper-boxed .container {
    width: 1100px;
  }
}

.header {
  position: relative;
}

.header::after {
  display: block;
  clear: both;
  content: "";
}

.header .container {
  position: relative;
}

.header-right-bottom {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.header-right-bottom > .menu-x {
  max-width: 100%;
  word-break: normal;
  word-wrap: break-word;
  white-space: normal;
  float: right;
}

.header-left {
  position: absolute;
  width: 240px;
  height: 100%;
}

.header-right {
  margin-left: 240px;
}

.has-sidebar-left,
.has-sidebar-right {
  position: relative;
  transition: left .4s ease-out, right .4s ease-out, background-image .2s ease-out;
}

.has-sidebar-left:not(.hide-left-always) > .page-wrapper {
  margin-left: 240px;
}

.has-sidebar-right:not(.hide-right-always) > .page-wrapper {
  margin-right: 240px;
}

.sidebar-left,
.sidebar-right {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 7;
  width: 240px;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  white-space: nowrap;
  background-color: white;
  transition: left .2s ease-out, right .2s ease-out;
}

.sidebar-left {
  left: 0;
}

.sidebar-right {
  right: 0;
}

.hide-left-always > .sidebar-left,
.hide-right-always > .sidebar-right {
  display: none;
}

@media (max-width: 767px) {
  .has-sidebar-left:not(.hide-left-always) > .page-wrapper {
    margin-left: 0;
  }
  .has-sidebar-right:not(.hide-right-always) > .page-wrapper {
    margin-right: 0;
  }
  .sidebar-left,
  .sidebar-right {
    position: fixed;
    height: 100vh;
  }
  .hide-left-always > .sidebar-left,
  .hide-right-always > .sidebar-right {
    display: block;
  }
  .sidebar-left {
    left: -240px;
  }
  .sidebar-right {
    right: -240px;
  }
  .hide-left > .sidebar-left {
    left: 0;
  }
  .hide-right > .sidebar-right {
    right: 0;
  }
  .hide-left > .sidebar-overlay,
  .hide-right > .sidebar-overlay {
    position: fixed;
    top: 0;
    z-index: 6;
    width: 100%;
    height: 100%;
    content: "";
    background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR4nGNiqAcAAIgAgoGu+rEAAAAASUVORK5CYII=");
    background-repeat: repeat;
  }
  .hide-left > .sidebar-overlay {
    left: 0;
  }
  .hide-right > .sidebar-overlay {
    right: 0;
  }
}

[technoart-toggle] {
  cursor: pointer;
  user-select: none;
}

.footer-fixed {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}

.has-sidebar-left .footer-fixed {
  left: 231px;
}

.has-sidebar-right .footer-fixed {
  right: 231px;
}

@media (max-width: 767px) {
  .has-sidebar-left .footer-fixed {
    left: 0;
  }
  .has-sidebar-right .footer-fixed {
    right: 0;
  }
}

